:root {
  --app-background: white;
  --error: #ff6f69;
  --letter-hover: #fff5f5;
  --letter-selected-background: #ececec;
  --text-color: #323232;
  --settings-width: 260px;
  --app-width: 260px
}

.dark {
  --app-background: #323232;
  --text-color: #ddd;
  --letter-hover: #404040;
}

body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;

  background-color: white;

  background-color: var(--app-background);
  transition: background-color linear 0.2s;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

#root {
  display: flex;
  flex-direction: row-reverse;
  flex-wrap: wrap-reverse;
}

.settings-block {
  display: flex;
  justify-content: center;
  flex-shrink: 0;
  flex-grow: 1;
}

.settings {
  margin: 5px;
  width: 260px;
  width: var(--settings-width);
  display: flex;
  flex-direction: column;
  color: #323232;
  color: var(--text-color);
}

.app-block {
  display: flex;
  justify-content: center;
  flex-grow: 10;
}
.app {
  width: var(--app-width);
}

.word {
  display: flex;
  justify-content: flex-start;
  flex-wrap: wrap;
  min-height: 64px;

}

.word__letter {
  margin: 2px;
}

.image {
  width: 100%;
  height: 260px;
  transition: opacity 0.1s ease-in-out;
}

.image_loading {
  opacity: .2;
}

.skipped {
  text-decoration: line-through;
}
.letter {
    width: 60px;
    height: 60px;
    border-radius: 3px;
    box-sizing: border-box;
    font-size: 30px;
    display: flex;
    justify-content: center;
    align-items: center;
    transition: 0.1s ease-in-out;
    -webkit-user-select: none;
       -moz-user-select: none;
        -ms-user-select: none;
            user-select: none;
    cursor: pointer;
    margin: 2px;
    color: var(--text-color);
}

.letter_selected {
    background: var(--letter-selected-background);
}

.letter_active:hover {
    background-color: var(--letter-hover);
}

.letter_wrong {
    background-color: var(--error);
}
.switch {
    position: relative;
    display: inline-block;
    height: 34px;
    line-height: 34px;
    font-size: 1.1rem;
    color: var(--text-color);
    -webkit-user-select: none;
       -moz-user-select: none;
        -ms-user-select: none;
            user-select: none;
    margin-bottom: 5px;
}

.switch input {
    opacity: 0;
    width: 0;
    height: 0;
}

.slider {
    position: relative;
    cursor: pointer;
    background-color: #ccc;
    transition: .2s;
    width: 60px;
    height: 100%;
    float: right;
}

.slider:before {
    position: absolute;
    content: "";
    height: 26px;
    width: 26px;
    left: 4px;
    bottom: 4px;
    background-color: white;
    transition: .2s;
}

input:checked+.slider {
    background-color: #8a7b46;
}

input:focus+.slider {
    box-shadow: 0 0 1p #8a7b46;
}

input:checked+.slider:before {
    -webkit-transform: translateX(26px);
            transform: translateX(26px);
}

/* Rounded sliders */
.slider.round {
    border-radius: 34px;
}

.slider.round:before {
    border-radius: 50%;
}
.btn {
  border: none;
  padding: 14px 28px;
  cursor: pointer;
  width: 100%;
  opacity: 1;
  font-size: 1.1rem;
}

.btn_default {
  background-color: var(--app-light-background);
  color: var(--text-color);
}

.btn_default:hover {
  opacity: .7;
}
